.advantages {
  padding: 120px 10px 123px;
  margin: 0 auto;
  max-width: 1242px;

  @include vp-1023 {
    padding: 80px 0 76px;
    max-width: 670px;
  }

  @include vp-767 {
    max-width: 670px;
    padding: 50px 25px 17px;
  }
}

.advantages__container ul {
  display: grid;
  grid-template-columns: auto auto;
  gap: 30px;

  @include vp-1023 {
    column-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: auto;
    gap: 30px;
  }
}

.advantages__container li {
  display: flex;
  flex-direction: column;
  max-width: 595px;
  min-height: 300px;
  flex-grow: 1;
  gap: 15px;
  color: $color-black-pearl;
  fill: $color-deep-sky-blue;
  padding: 42px 45px 35px 42px;
  border: 1px solid $color-whisper;
  border-left: 5px solid $color-deep-sky-blue;

  @include vp-1023 {
    max-width: 320px;
    padding: 40px 43px 30px 40px;
    gap: 7px;
  }

  @include vp-767 {
    max-width: 670px;
    padding: 30px 35px 35px 25px;
    min-height: 340px;
  }
}

.advantages__container li:hover {
  background: $color-deep-sky-blue;
  color: $color-header-white;
  fill: $color-header-white;

  & h2 {
    color: $color-header-white;
  }
}

.advantages__container svg {
  fill: inherit;
  margin-bottom: 5px;

  @include vp-1023 {
    margin-bottom: 6px;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;
    margin-bottom: 8px;
  }
}

.advantages__container h3 {
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  font-family: $ff-roboto;
  color: $color-deep-sky-blue;
  margin: 0;

  @include vp-1023 {
    font-size: 24px;
    line-height: 34px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__container p {
  margin: 0;
  color: inherit;
  line-height: 22px;
  text-align: justify;

  @include vp-1023 {
    min-height: 165px;
    text-align: start;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
    min-height: 92px;
  }
}
